/* Import the google web fonts you want to use */

  @import url('https://fonts.googleapis.com/css2?family=Carter+One&family=Forum&family=Quattrocento&display=swap');


/* FONTS
font-family: "Nunito", sans-serif;
font-family: "Quicksand", sans-serif;
font-family: "Roboto", sans-serif; 
*/

/* COLOR PALETTE */
/* Feel free to change these values and create your own color palette! https://coolors.co/ is a great resource */
:root {
  --primary: rgb(134, 5, 89);
  --secondary: rgb(144, 191, 210);
  --backgroundGradient: linear-gradient(92.83deg, rgb(231, 162, 72) 0, #f93a13 100%)
  --info: #fff;
  --warning: #f6511d;
  --white: #ffffff;
  --offWhite: #f2f2f3;
  --dark: #3b3b3b;
  --outline: #c3c0c0;
  --appBackground: rgb(231, 162, 72);
}

/* GLOBAL STYLES */
body,
button,
input,
select,
textarea {
  font-family: "Forum", sans-serif;
}

body {
  /* background: radial-gradient(circle, rgba(231,162,72,1) 0%, rgba(249,57,19,1) 37%, rgba(231,162,72,1) 89%); */
  background-color: var(--appBackground);
  margin: 0;
  -webkit-user-select: none; /* Safari */
  -moz-user-select: none; /* Firefox */
  -ms-user-select: none; /* Internet Explorer/Edge */
  user-select: none; /* Non-prefixed version, currently supported by Chrome, Opera and Firefox */
  
}


h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: "Forum", serif;
}

/* This prevents all the links in the app from having default underline */
a {
  text-decoration: none;
  color: inherit;
}
/* This prevents all the links in the app from turning purple after visiting */
a:visited {
  color: inherit;
}
/* This prevents all the inputs in the app from having that blue outline when in focus */
input:focus {
  outline: none;
}

/* GLOBAL STYLING FOR ALL THE BUTTONS BUTTONS */
button {
  padding: 0.5rem 1rem;
  border: 1px solid transparent;
  border-radius: 0.5rem;
  color: var(--white);
  cursor: pointer;
}

/* These are all the color options for the buttons */
/* If you change the color variables, be sure to change the hover colors to be a darker shade of the colors you chose*/
.btn-primary {
  background-color: var(--primary);
}

.btn-primary:hover {
  background-color: var(--appBackground);
}

.btn-secondary {
  background-color: var(--secondary);
}

.btn-secondary:hover {
  background-color: var(--appBackground);
}

.btn-warning {
  background-color: var(--warning);
}

.btn-warning:hover {
  background-color: #141212;
  color: var(--warning);
  font-weight: bold;
}

.btn-info {
  background-color: var(--info);
}

.btn-info:hover {
  background-color: #71a500;
}

/* to disable scrollbar visibility */
::-webkit-scrollbar {
  display: none;
}

